import React from 'react';
import { FaQuoteLeft } from 'react-icons/fa';
import { QuoteConsumer } from '../contexts/quoteContext';

function Quote() {
  return (
    <QuoteConsumer>
      {({ randomColor, quoteObj: { quote, author }, quoteChanged }) => (
        <div
          className={quoteChanged ? 'quote fade' : 'quote'}
          style={{ color: randomColor }}
        >
          <div className="quote-box">
            <p className="quote-text">
              <FaQuoteLeft />
              &nbsp;{quote}
            </p>
          </div>
          <div className="quote-author">
            <span>- {author}</span>
          </div>
        </div>
      )}
    </QuoteConsumer>
  );
}

export default Quote;
