import React from 'react';
import { ImShuffle } from 'react-icons/im';
import { QuoteConsumer } from '../contexts/quoteContext';

function Header() {
  return (
    <QuoteConsumer>
      {({ isLoading, quoteObj, generateQuote }) => (
        <>
          <h1 className="heading-primary">Quote of the Day</h1>
          {!isLoading && (
            <h3 className="heading-tertiary">
              Find a quote that resonates with you and share it with the world!
            </h3>
          )}
          {!isLoading && !quoteObj && (
            <>
              <button className="click-cta" onClick={generateQuote}>
                <ImShuffle />
              </button>
              <p className="enter-cta">
                Click the "SHUFFLE" button to get your quote!
              </p>
              <p className="enter-cta mob">
                (or press the "ENTER" or "SPACEBAR" keys )
              </p>
            </>
          )}
        </>
      )}
    </QuoteConsumer>
  );
}

export default Header;
